@import url(https://fonts.googleapis.com/css?family=Roboto);
.container{
    display: flex;
    height: calc( 100vh - 64px );
    align-items: center;
    justify-content: center;
}
.main{
    width: 30vw !important;
    height: 30vw !important;
}
/*@import "../../../shared/css/var/var.css";*/

.container{
    height: calc( 100vh - 64px - 40px );
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px;
}

.layer{
    display: flex;
    color: white;
    justify-content: center;
    align-items: center;
    img{
	border-radius: 50%;
    }
}

.layer1{
    height: calc( 40vh - 20px);
    img {
	max-width: 40vw;
	max-height: 40vh;
    }
}
.layer2{
    display:flex;
    height: 20vh;

}
.layer3{
    height: calc( 40vh - 20px);
    img {
	max-width: 35vw;
	max-height: 35vh;
    }
}

.main{
    margin:0;
    align-content:center;
    padding-left:3vw;
    height: calc( 100vh - 64px - 48px );
    padding-right:3vw;
    display:flex;
    justify-content:center;
    align-items:center;
    h2{color:white;}
    .center{
	padding-left: 5vw;
	padding-right:5vw;
	height: calc(  calc( 100vh - 64px - 48px) * 0.8);
	.controls   {
	    height: 30%;
	    > div{
		display:flex;
		align-items: center;
		justify-content: center;
		button{
		    width: 15vw !important;
		    height: 15vw !important;
		    .grey{
			fill: grey !important; 
		    }
		    svg{
			width: 10vw !important;
			height: 10vw !important;
		    }
		}
	    }
	}
	.now{
	    height: 70%;
	    .image{
		display:flex;
		justify-content:center;
		align-items:center;
	    }
	    img{
		height:35vh;
	    }
	    p {
		text-align: center;
		color: white;
	    }
	}
	.volume{
	    margin-bottom:0px;
	    > div {
		margin-bottom:0px !important;
	    }
	}
    }
}

.container{
    height: calc( 100vh - 64px - 48px );
    overflow:hidden;
    .float{
	position: absolute;
	top:75% !important;
	left: 180%;
	z-index:2;
    }
    .content{
	height: calc( 100vh - 64px - 48px - 48px);
	display: flex;
	justify-content: center;
	align-items: center;
	.image{
	    display: flex;
	    justify-content: center;
	    align-items: center;
	    img{
		border-radius: 30%;
		max-height:44vh;
		max-width:50vw;
	    }
	}
	.data{
	    display: flex;
	    flex-direction: column;
	    justify-content: center;
	    align-items: center;
	}

	.playIcon{
	    height:120px !important;
	    width:120px !important;
	}
	.playContainer{
	    width: 100% !important;
	}
    }
    .listWrapper{
	display:flex;
	align-items:stretch;
	flex: 1 1 auto; 
    }
    .listFull{
	height: calc( 100vh - 64px - 48px);
    }
    .listLow{
	height: calc( 100vh - 64px -48px - 150px);
    }
    .nav{
	width: 100vw;
	z-index: 30;
	/* border-bottom: 1px solid #1ED760; */
    }
}


