  padding-left: 3vw;
  padding-right: 3vw;
  background-color: black;
}

/*height: $util;*/
.container .dashboard {
  /*height: $ mainButton;*/
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
}

.container .dashboard div {
  height: 80% !important;
}

.container .dashboard button {
  height: 100% !important;
  display: flex !important;
  justify-content: center;
}

.hate {
  /*height: $ hateHeight;*/
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  flex-direction: column;
}

.hateButton {
  transform: scale(2.0) !important;
}

